/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem 2rem;
  z-index: 1;
  width: 100%;
  margin-bottom: 1rem;
}

.navbar__title {
  flex: 1;
}

.navbar ul {
  display: flex;
  list-style-type: none;
}

.navbar a {
  color: rgb(0, 0, 0);
  padding: 0.45rem;
  margin: 0 0.25rem;
}

.navbar a:hover {
  color: orangered;
}

/* Search form */

.search-form {
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-form__input {
  width: 500px;
  padding: 1rem;
  border: 2px solid #333;
  outline: none;
  margin: 0.5rem;
  border-radius: 0.75rem;
}

.search-form__input:focus {
  border: 2px solid rgb(18, 195, 2);
}

.search-form__btn {
  padding: 1rem;
  outline: none;
  border: none;
  border-radius: 0.75rem;
  background-color: coral;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

.search-form__btn:hover {
  background-color: rgb(251, 71, 5);
}

/* Recipes */

.recipes {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.recipe-card {
  /* width: 320px; */
  text-align: center;

  margin: 50px;
  padding: 2rem;
  background-color: #e0e5ec;
  border-radius: 25px;
  box-shadow: 9px 9px 16px rgb(163, 177, 198, 0.6),
    -9px -9px 16px rgba(255, 255, 255, 0.5);
}

.card-img-top:hover {
  transform: scale(1);
  filter: contrast(200%);
}

.card-img-top {
  margin: 15px 0;
  border-radius: 25px;
  transform: scale(0.9);
  transition: all 0.2s linear;
}

@media screen and (max-width: 560px) {
  .navbar {
    flex-direction: column;
  }
}
